import './App.css';
import axios from 'axios';
import { useEffect } from 'react';
// const BASE_URL = 'http://localhost:8000/'
const BASE_URL = 'https://csgo-site.herokuapp.com/'
function App() {
function login() {
  window.location.replace(`${BASE_URL}accounts/steam/login/`)
  }

  function getCookie(name) {
  let matches = document.cookie.match(new RegExp(
    "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ));
  return matches ? decodeURIComponent(matches[1]) : undefined;
}
  useEffect(() =>{
    console.log(getCookie('sessionid'));
  })

  function request() { 
    axios.get(`${BASE_URL}api/me/`, {withCredentials: true}).then((resp) => console.log(resp))
   }

  return (
    <div className="App">
    <h6 className="header" onClick={request}>ЖМИТЕ НЕ СКАМ!!!</h6>
      <img src="https://community.cloudflare.steamstatic.com/public/images/signinthroughsteam/sits_02.png" onClick={login}className="login-btn"/>
    </div>
  );
}

export default App;
